.form-field {
  box-sizing: border-box;
  display: block;
  padding: 14px 16px;
  border: 1px solid transparent;
  width: 100%;
  height: 48px;
  border-radius: 40px;
  background-color: #F8F8FA;
  font-family: 'Poppins';
  outline: none;
  color:#292929;

  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #7E7E83;
  }

  &:hover {
    border-color: #D5D7DE;
  }

  &:focus {
    border-color: transparent;
  }

  &--text {
    margin-bottom: 32px;
    height: 148px;
    border-radius: 16px;
    resize: none;
  }
}
